
/**
* 支付成功
* @author zml
* @version 2017-07-25
*/

import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {

name: 'app-error',

components: {
XButton,
Icon,
},

data() {
return {
statusData: [
{
code: 500,
text: '服务器繁忙，请稍后再试',
},
{
code: 403,
text: '用户未登录，请先登录',
},
{
code: 404,
text: 'Not Found !!!',
},
{
code: 405,
text: '该用户已在其他设备登录',
},
],
};
},

computed: {
status() {
return +this.$router.currentRoute.params.status;
},
},

created() {

},

};



