
/**
* 充值
* @author zml
* @version 2017-07-25
*/

import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {

name: 'app-pay-index',

data() {
return {
charge_list: [], // 充值金额列表
id: null, // 充值金额id
loading: false, // 充值按钮loading
loadingText: '确认充值',

appId: '', // appId唯一标识
timeStamp: '', // 支付签名时间戳
nonceStr: '', // 支付签名随机串
package: '', // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
paySign: '', // 支付签名
};
},

components: {
XButton,
Flexbox,
FlexboxItem,
},

computed: {
code() {

return decodeURIComponent((new RegExp('[?|&]code=' + '([^&;]+?)(&|#|;|$)').exec(window.location.href) || [, ''])[1].replace(/\+/g, '%20')) || null;
},
},

methods: {

choseRechargePrice(price, id) {
this.id = id;
this.$vux.toast.text(`您选择了${price}`, 'middle');
},


beforeWxRecharge() {
if (this.id) {
this.getWxpayParam();
} else {
this.$vux.toast.text('请先选择充值金额', 'top');
}
},


getWxpayParam() {
this.loading = true;
this.loadingText = '正在处理中...';

this.$http.post('/mobileAppPay!getMppayParam.do', {
type: 1,
sign_id: this.id,
code: this.code,
})
.then(({ pay_message }) => {
console.log(+pay_message.mch_id);
this.loading = false;
this.loadingText = '确认充值';

this.appId = pay_message.appid;
this.timeStamp = +pay_message.mch_id;
this.nonceStr = pay_message.nonce_str;
this.package = pay_message.package;
this.paySign = pay_message.sign;

this.prepayid = pay_message.prepay_id;

this.chooseWXPay();
})
.catch((error) => {
this.loading = false;
this.loadingText = '确认充值';
this.$vux.toast.text(error, 'top');
});
},


chooseWXPay() {
this.$wechat.chooseWXPay({
appId: this.appId,

timeStamp: this.timeStamp,
nonceStr: this.nonceStr, // 支付签名随机串，不长于 32 位
package: `prepay_id=${this.prepayid}`, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign: this.paySign, // 支付签名

success(res) {
console.log(res);
this.$router.push('/pay/ok');
},
});
},


getRechargeList() {
this.$http.get('/mobileEntiretySystem!getRechargeList.do')
.then((res) => {
this.charge_list = res.charge_list;
})
.catch((error) => {
this.$vux.toast.text(error, 'top');
});
},
},

created() {
this.getRechargeList();
},

};



